import classNames from 'classnames/bind';
import styles from './Login.module.scss';
import { Routes, Route } from 'react-router-dom';

import {
    FaceBookColor,
    GoogleColor,
    QRCode,
    UserIcon,
    TwitterColor,
    LINEColor,
    KakaoTalkColor,
    Apple,
    InstagramColor,
} from '../../components/Icons';

import Header from './Header';
import Footer from './Footer';
import DivBox from '../../components/DivBox';

import ByEmail from './LoginByEmail';

import { Link } from 'react-router-dom';

const cx = classNames.bind(styles);

function Login() {
    window.scrollTo(0, 0);
    return (
        <div className={cx('wrapper')}>
            <Header />
            <Routes>
                <Route
                    key={1}
                    path="/"
                    element={
                        <div className={cx('body')}>
                            <div className={cx('body_wrapper')}>
                                <div>
                                    <h2 className={cx('login_title')}>ng nhp vo TikTok</h2>
                                    <div className={cx('login_description')}>
                                        Qun l ti khon, kim tra thng bo, bnh lun trn cc video, v.v.
                                    </div>
                                    <div className={cx('chanel_item')}>
                                        <DivBox icon={<QRCode />}>S dng m QR</DivBox>
                                        <Link to={'/login/email'}>
                                            <DivBox icon={<UserIcon />}>S in thoi / Email / TikTok ID</DivBox>
                                        </Link>
                                        <DivBox icon={<FaceBookColor />}>Tip tc vi Facebook</DivBox>
                                        <DivBox icon={<GoogleColor />}>Tip tc vi Google</DivBox>
                                        <DivBox icon={<TwitterColor />}>Tip tc vi Twitter</DivBox>
                                        <DivBox icon={<LINEColor />}>Tip tc vi LINE</DivBox>
                                        <DivBox icon={<KakaoTalkColor />}>Tip tc vi KakaoTalk</DivBox>
                                        <DivBox icon={<Apple />}>Tip tc vi Apple</DivBox>
                                        <DivBox icon={<InstagramColor />}>Tip tc vi Instagram</DivBox>
                                    </div>
                                </div>
                            </div>
                        </div>
                    }
                ></Route>

                <Route
                    key={2}
                    path="/email"
                    element={
                        <div className={cx('body')}>
                            <div className={cx('body_wrapper')}>
                                <ByEmail />
                            </div>
                        </div>
                    }
                ></Route>
            </Routes>

            <Footer />
        </div>
    );
}
export default Login;
