import classNames from 'classnames/bind';
import styles from '../PopperLogin.module.scss';

import {
    FaceBookColor,
    GoogleColor,
    InstagramColor,
    KakaoTalkColor,
    LINEColor,
    TwitterColor,
    UserIcon,
} from '../../Icons';

const cx = classNames.bind(styles);

function Register({ setInner }) {
    return (
        <div className={cx('content')}>
            <h1 className={cx('header')}>ng K TikTok</h1>

            <button
                className={cx('divBoxBtn')}
                onClick={() => {
                    setInner(5);
                }}
            >
                <span>
                    <UserIcon className={cx('icon')}></UserIcon>
                </span>
                <span className={cx('title')}>S dng s in thoi hoc Email</span>
            </button>

            <button className={cx(['divBoxBtn', 'disable'])}>
                <span>
                    <FaceBookColor className={cx('icon')}></FaceBookColor>
                </span>
                <span className={cx('title')}>Tip tc vi Facebook</span>
            </button>
            <button className={cx(['divBoxBtn', 'disable'])}>
                <span>
                    <GoogleColor className={cx('icon')}></GoogleColor>
                </span>
                <span className={cx('title')}>Tip tc vi Google</span>
            </button>

            <button className={cx(['divBoxBtn', 'disable'])}>
                <span>
                    <TwitterColor className={cx('icon')}></TwitterColor>
                </span>
                <span className={cx('title')}>Tip tc vi Twitter</span>
            </button>

            <button className={cx(['divBoxBtn', 'disable'])}>
                <span>
                    <LINEColor className={cx('icon')}></LINEColor>
                </span>
                <span className={cx('title')}>Tip tc vi Twitter</span>
            </button>
            <button className={cx(['divBoxBtn', 'disable'])}>
                <span>
                    <KakaoTalkColor className={cx('icon')}></KakaoTalkColor>
                </span>
                <span className={cx('title')}>Tip tc vi KakaoTalk</span>
            </button>

            <button disabled className={cx(['divBoxBtn', 'disable'])}>
                <span>
                    <InstagramColor className={cx('icon')}></InstagramColor>
                </span>
                <span className={cx('title')}>Tip tc vi Twitter</span>
            </button>
        </div>
    );
}

export default Register;
