import classNames from 'classnames/bind';
import styles from './following.module.scss';
import { Route, Routes, useNavigate } from 'react-router-dom';
import { useEffect, useState, useContext } from 'react';
import { ThemeContext } from '../../Context';
import { getCookie } from '../../service/local/cookie';
import * as VideoServices from '../../service/videoServices';
import InfiniteScroll from 'react-infinite-scroll-component';
import Post from '../../components/Post';
import Video from '../Video';
import Loading from '../../components/loading';

const cx = classNames.bind(styles);

function Following() {
  const [items, setItems] = useState([]); // D liu hin ti
  const [hasMore, setHasMore] = useState(true); // C thm d liu  load khng
  const [page, setPage] = useState(1); // S trang hin ti
  const context = useContext(ThemeContext);

  const navigate = useNavigate();

  const fetchMoreData = () => {
    const authToken = getCookie('authToken') || '';
    VideoServices.getFollowingVideos({ page: page, token: authToken })
      .then((data) => {
        setItems((prevItems) => [...prevItems, ...data]);
        page === 1 ? context.setListVideo(data) : context.setListVideo((prevState) => [...prevState, ...data]);
        if (data.length === 0) {
          setHasMore(false);
        }
        setPage((prevPage) => prevPage + 1);
      })
      .catch((err) => {
        console.log(err);
      });
  };

  useEffect(() => {
    window.scroll(0, 0);
    const authToken = getCookie('authToken') || '';
    if (!authToken) {
      navigate('/login');
    } else {
      fetchMoreData();
    }
    // eslint-disable-next-line
  }, []);
  return (
    <div className={cx('container')}>
      <Routes>
        <Route path="/video/:id" key={100} element={<Video routeBack="/following" mainRoute="/following" />} />
      </Routes>
      <InfiniteScroll
        dataLength={items.length} // S lng phn t hin ti trong danh sch d liu
        next={fetchMoreData} // Callback c gi khi ngi dng cun n cui trang
        hasMore={hasMore} // Cn d liu  load khng
        loader={
          <div className={cx('loader-container')}>
            <Loading />
          </div>
        }
      >
        {items.map((item, index) => (
          <Post
            route="/following"
            key={index}
            data={item}
            volumeValue={context.volume}
            ChangeVolumeGlobal={context.setVolume}
          ></Post>
        ))}
      </InfiniteScroll>
    </div>
  );
}
export default Following;
