import classNames from 'classnames/bind';
import styles from './SignupByEmail.module.scss';

import { Link } from 'react-router-dom';
import Button from '../../../components/Button';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faAngleLeft } from '@fortawesome/free-solid-svg-icons';

const cx = classNames.bind(styles);

function SisnupByEmail() {
    return (
        <div className={cx('wrapper')}>
            <h2 className={cx('title')}>ng k TikTok</h2>
            <div className={cx('description')}>
                Email
                <Link>ng ky bng s in thoi</Link>
            </div>
            <div className={cx('form-container')}>
                <div className={cx('input-container')}>
                    <input type="text" placeholder="a ch email" />
                </div>
                <div className={cx('input-container')}>
                    <input type="password" placeholder="Mt Khu" />
                </div>
                <div className={cx('input-container')}>
                    <input type="password" placeholder="Xc Nhn Mt Khu" />
                </div>
                <div className={cx('confirm-container')}>
                    <div className={cx('checkbox-wrapper')}>
                        <input type="checkbox"></input>
                    </div>

                    <label className={cx('description')}>
                        Nhn ni dung thnh hnh, bn tin, khuyn mi,  xut v thng tin cp nht ti khon c gi
                        n email ca bn
                    </label>
                </div>
                <Button className={cx('login-btn')} primary>
                    Tip
                </Button>
            </div>

            <div className={cx('back-container')}>
                <FontAwesomeIcon icon={faAngleLeft} className={cx('left-icon')} />
                <Link to={'/signup'}>Quay li</Link>
            </div>
        </div>
    );
}

export default SisnupByEmail;
