import classNames from 'classnames/bind';
import styles from './UploadStatus.module.scss';

const cx = classNames.bind(styles);

function Progress() {
  return (
    <div className={cx('progress-wrapper')}>
      <div className={cx('loading')}>
        <div
          style={{
            '--percent': `99%`,
          }}
          className={cx('cicle')}
        ></div>
        <div className={cx('over')}></div>
        <div className={cx('percent')}>99%</div>
      </div>
      <div className={cx('status-title')}>ang ti ln...</div>
      <div className={cx('description')}>Vic bn ri trang khng gy gin on cho qu trnh ng video</div>
    </div>
  );
}
export default Progress;
