import classNames from 'classnames/bind';
import styles from './home.module.scss';
import { useEffect, useContext, useState } from 'react';
import { ThemeContext } from '../../Context';
import { getCookie } from '../../service/local/cookie';
import * as VideoServices from '../../service/videoServices';
import InfiniteScroll from 'react-infinite-scroll-component';
import Post from '../../components/Post/post';
import Loading from '../../components/loading';
import { Route, Routes } from 'react-router-dom';
import Video from '../Video';

const cx = classNames.bind(styles);
function Home() {
  const [items, setItems] = useState([]); // D liu hin ti
  const [hasMore, setHasMore] = useState(true); // C thm d liu  load khng
  const [page, setPage] = useState(Math.floor(Math.random() * 10) + 1); // S trang hin ti
  const context = useContext(ThemeContext);
  const [theFirst, setTheFirst] = useState(true);

  const fetchMoreData = () => {
    // Thc hin logic  ly d liu mi  y, v d:
    // Gi API hoc thao tc vi d liu  c

    // Sau khi ly c d liu mi, cp nht state items
    // v kim tra xem cn d liu na khng
    const authToken = getCookie('authToken') || '';
    VideoServices.getVideos({ type: 'for-you', page: page, token: authToken })
      .then((data) => {
        setItems((prevItems) => [...prevItems, ...data]);
        if (theFirst) {
          context.setListVideo(data);
          setTheFirst(false);
        } else {
          context.setListVideo((prevState) => [...prevState, ...data]);
        }
        // Nu khng c d liu mi na, t hasMore thnh false
        // iu ny s ngn ngi dng cun  load thm d liu
        if (data.length === 0) {
          setHasMore(false);
        }
        // Tng s trang ln 1

        setPage((prevPage) => prevPage + 1);
      })
      .catch((err) => {
        console.log(err);
      });
  };

  // start
  useEffect(() => {
    window.scroll(0, 0);

    // eslint-disable-next-line
    fetchMoreData();
    // eslint-disable-next-line
  }, []);

  return (
    <div className={cx('container')}>
      <Routes>
        <Route path="/video/:id" key={100} element={<Video />} />
      </Routes>
      <InfiniteScroll
        dataLength={items.length} // S lng phn t hin ti trong danh sch d liu
        next={fetchMoreData} // Callback c gi khi ngi dng cun n cui trang
        hasMore={hasMore} // Cn d liu  load khng
        loader={
          <div className={cx('loader-container')}>
            <Loading />
          </div>
        } // Hin th loader khi ang ti d liu
        // scrollThreshold={'200px'}
      >
        {/* Hin th danh sch d liu */}
        {items.map((item, index) => (
          <Post key={index} data={item} volumeValue={context.volume} ChangeVolumeGlobal={context.setVolume}></Post>
        ))}
      </InfiniteScroll>
    </div>
  );
}
export default Home;
