import classNames from 'classnames/bind';
import styles from '../PopperLogin.module.scss';
import { useState, useContext } from 'react';
import { ThemeContext } from '../../../Context';
import { login } from '../../../service/loginServices';
import * as Cookie from '../../../service/local/cookie';

const cx = classNames.bind(styles);

function PhoneNumber() {
    const [email, setEmail] = useState('');
    const [passWord, setPassword] = useState('');
    const [loginFail, setLoginFail] = useState(false);
    const context = useContext(ThemeContext);

    const checkFill = () => {
        if (email.trim() !== '' && passWord.trim() !== '') {
            return true;
        }
        return false;
    };

    return (
        <div className={cx('content')}>
            <h1 className={cx('header')}>ng nhp </h1>
            <form className={cx('form-login')}>
                <div className={cx('form-header')}>
                    <label>Email hoc TikTok ID</label>
                </div>
                <div className={cx('form-input')}>
                    <input
                        name="email"
                        placeholder="Email hoc TikTokID"
                        onChange={(e) => {
                            setEmail(e.target.value);
                        }}
                    />
                </div>
                <div className={cx('form-input')}>
                    <input
                        name="password"
                        type="password"
                        autoComplete="on"
                        placeholder="Mt Khu"
                        onChange={(e) => {
                            setPassword(e.target.value);
                        }}
                    />
                </div>
                {loginFail && <p className={cx('login-fail')}>Ti khon hoc mt khu khng chnh xc !</p>}

                <span className={cx('forgot-pass')}>Qun Mt Khu ?</span>

                <button
                    className={checkFill() === true ? cx(['submit-btn', 'primary']) : cx('submit-btn')}
                    onClick={(e) => {
                        e.preventDefault();

                        login({ email: email, password: passWord })
                            .then((res) => {
                                if (res.data) {
                                    context.setNotiLoginSuccess(true);
                                    setTimeout(() => {
                                        context.setNotiLoginSuccess(false);
                                    }, 2000);
                                    setLoginFail(false);
                                    Cookie.setToken({ token: res.meta.token });
                                    context.setCurrentUser(true);
                                    context.setLoginPopper(false);
                                    context.setUser(res.data);
                                } else {
                                    setLoginFail(true);
                                }
                            })
                            .catch((err) => {
                                console.log(err);
                            });
                    }}
                >
                    <span>ng Nhp</span>
                </button>
            </form>
        </div>
    );
}

export default PhoneNumber;
