import classNames from 'classnames/bind';
import styles from '../../PopperLogin.module.scss';

import { useState } from 'react';

const cx = classNames.bind(styles);

function ByPhoneNumber() {
    const [email, setEmail] = useState('');
    const [passWord, setPassword] = useState('');

    const checkFill = () => {
        if (email.trim() !== '' && passWord.trim() !== '') {
            return true;
        }
        return false;
    };

    return (
        <div className={cx('content')}>
            <h1 className={cx('header')}>ng K</h1>
            <form className={cx('form-login')}>
                <div className={cx('form-header')}>
                    <label>Email </label>
                </div>
                <div className={cx('form-input')}>
                    <input
                        placeholder="a ch email"
                        onChange={(e) => {
                            setEmail(e.target.value);
                        }}
                    />
                </div>
                <div className={cx('form-input')}>
                    <input
                        placeholder="Mt Khu"
                        onChange={(e) => {
                            setPassword(e.target.value);
                        }}
                    />
                </div>

                <div className={cx('form-confirm')}>
                    <input type="checkbox" />
                    <p className={cx('confirm-desc')}>
                        Nhn ni dung thnh hnh, bn tin, khuyn mi,  xut v thng tin cp nht ti khon c gi
                        n email ca bn
                    </p>
                </div>

                <button
                    className={checkFill() === true ? cx(['submit-btn', 'primary']) : cx('submit-btn')}
                    onClick={(e) => {
                        e.preventDefault();
                    }}
                >
                    <span>ng K</span>
                </button>
            </form>
        </div>
    );
}

export default ByPhoneNumber;
